<!--公共主页，用于判断服务器状态、身份选择等-->
<template>
  <div class="main">
    <div style="margin-bottom: 20px;">
      <span style="font-size: 18px;font-weight: bold;">设置服务器地址：</span>
    </div>
    <div>
      <a-input v-model="serverIP"
               @pressEnter="onClick"
               :placeholder="defaultServerIP"
               size="large"
               :disabled="connecting">
      </a-input>
      <a-button
        block
        @click="onClick"
        type="primary"
        :loading="connecting"
        size="large"
        style="margin-top: 20px">
        {{ connecting?'连接中':'确认' }}
      </a-button>
    </div>
    <a-modal
      title="身份选择："
      v-model="visible"
      :footer="null"
      :maskClosable="false">
      <div style="margin: 20px 0">
        <a-button block @click="competitorReg" type="primary" size="large">
          我是选手
        </a-button>
        <a-button block @click="judgeReg" type="primary" style="margin-top: 20px" size="large">
          我是评委
        </a-button>
      </div>
    </a-modal>
  </div>
</template>

<script>
  export default {
    name: "PublicIndex",
    data() {
      return {
        serverIP: '',
        connecting: false,//标记连接状态
        visible: false,
        defaultServerIP: '127.0.0.1',

      }
    },
    mounted() {
      //检测服务器状态

    },
    methods: {
      judgeReg() {

      },
      competitorReg() {
        this.$router.push('/competitor');
      },
      onClick() {
        this.connecting = true;
        this.serverIP = this.serverIP.length === 0 ? this.defaultServerIP : this.serverIP;
        this.connectHandler();
      },
      connectHandler() {
        //尝试连接服务器
        WS = new WebSocket("ws://" + this.serverIP + ":2000");
        WS.onopen = () => {
          this.connecting = false;
          //ws.send('connected');
          //选择身份
          this.visible = true;

          //心跳
          setInterval(() => {
            WS.send(JSON.stringify({code: 0, data: null}));
          }, 30000);
        };
        WS.onerror = () => {
          this.connecting = false;
          this.$error({
            title: '连接失败',
            content: '请检查网络连接状态或IP是否正确',
            maskClosable: true
          });
        };
        WS.onmessage = (e) => {
          let data = JSON.parse(e.data);
          if (data.code === 1) {
            //过滤通知
            this.$info({
              title: '通知',
              content: data.data,
              maskClosable: true
            });
          }
        };
        WS.onclose = () => {
          this.$error({
            title: '警告',
            content: "与服务器的连接断开",
            maskClosable: false,
            onOk() {
              window.location.href = '/';
            },
          });
        };
      }
    }
  }
</script>

<style scoped>
  .main {
    padding-top: 200px;
    width: 320px;
    margin: 0 auto;
  }
</style>



